.counter-banner {
    background-color: #161616;
    padding: 85px 0;
    color: #fff;
    font-weight: 500;

    &__container {
        text-align: center;

        span {
            &:first-of-type {}

            &:last-of-type {
                font-weight: 500;

                &>span {
                    color: #ff0000;
                }
            }
        }
    }

    &__count {
        font-size: 82px;
        font-weight: 400;
        margin: 20px 0;
    }

    @media(--phone) {
        padding: 35px 0;

        &__count {
            font-size: 42px;
        }
    }
}