.main-banner {

    &__container {
        position: relative;
    }

    &__wrapper {
        // min-height: 100vh;
        display: flex;
        flex-flow: row wrap;
        align-items: center;
        position: initial;
    }

    &__left {
        max-width: 55%;
        padding-bottom: 85px;
        padding-top: 250px;

        h3 {
            font-size: 44px;
            font-weight: 400;
            margin: 0 0 35px;
        }

        p {
            font-size: 24px;
            color: rgba(#6a6a6a, .7);
            line-height: 24px;
            margin-bottom: 50px;
        }
    }

    &__buttons {
        margin-bottom: 120px;
    }

    &__button {
        height: 50px;
        width: 210px;
        font-size: 16px;

        &_color {
            background-color: var(--blue);
            color: #fff;
            margin-right: 15px;

            &:hover {
                background-color: transparent;
                border-color: var(--blue);
                color: var(--blue);
            }
        }

        &_transparent {
            border-color: var(--blue);

            &:hover {
                background-color: var(--blue);
                color: #fff;
            }
        }
    }

    &__soccial {
        color: rgba(#000, .7);

        a {
            color: rgba(#000, .7);
            margin: 0 5px;

            &:hover {
                text-decoration: underline;
            }
        }
    }

    &__right {
        max-width: 45%;
        width: 100%;
        background-color: #d6d6d6;
        position: absolute;
        height: 100%;
        display: flex;
        align-items: center;
        right: 0;
    }

    &__slider {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;

        &-container {
            width: 100%;
            height: 100%;
        }

        &-wrapper {}

        &-slide {
            display: flex;
            flex-flow: row wrap;
            justify-content: center;
            align-items: center;

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        &-navigation {
            position: absolute;
            bottom: 15%;
            left: 50%;
            transform: translateX(-50%);
            display: flex;
            align-items: center;
            justify-self: center;
            z-index: 1;
        }

        &-arrow {
            background-image: url('../images/arrow.png');
            background-repeat: no-repeat;
            background-position: center;
            background-size: 8px;
            width: 25px;
            height: 25px;
            outline: none;

            &_prev {}

            &_next {
                transform: scaleX(-1);
            }
        }

        &-pagination {
            width: 220px;
            height: 4px;
            position: initial;
            margin: 0 10px;

            &.swiper-pagination-progressbar {
                background-color: #fff;

                .swiper-pagination-progressbar-fill {
                    background-color: #1e55d2;
                }
            }
        }

        &-counter {
            position: absolute;
            left: 0;
            right: 0;
            text-align: center;
            top: -15px;
            font-size: 16px;
            color: #fff;

            span {
                &.current {
                    color: var(--blue);
                }

                &.max {}
            }

        }

    }

    @media(--tablet) {
        padding-top: 150px;

        &__wrapper {
            min-height: initial;
            flex-direction: column;
            align-items: flex-start;
        }

        &__right {
            position: initial;
            display: none;
        }

        &__left {
            max-width: initial;
            width: 100%;
        }

        &__buttons {
            margin-bottom: 20px;
        }
    }


    @media(--phone) {
        padding-top: 170px;

        &__left {
            h3 {
                font-size: 36px;
                margin-bottom: 15px;
            }

            p {
                font-size: 16px;
                line-height: 18px;
            }
        }
    }

    @media(--sm-phone) {
        &__button {
            width: 100%;
            margin-bottom: 20px;
        }
    }

}