.steps {
    padding-top: 60px;
    padding-bottom: 60px;
    background-color: #161616;

    &__container {}

    &__wrapper {}

    &__head {
        padding-bottom: 70px;
        font-size: 28px;
        font-weight: 500;
        color: #fff;
    }


    &__inner {
        margin-bottom: -40px;
        display: flex;
        flex-flow: row wrap;
        justify-content: center;
    }

    &__block {
        position: relative;
        color: #fff;
        max-width: calc(100% / 3);
        width: 100%;
        text-align: center;
        margin-bottom: 40px;
        font-weight: 500;
    }

    &__count {
        position: absolute;
        font-size: 92px;
        font-weight: 700;
        opacity: 0.17;
        left: calc(50% - 45px);
        top: 5px;
        transform: translate(-50%, 0);
        line-height: 70px;
    }

    &__icon {
        margin-bottom: 20px;
        max-height: 75px;
    }

    &__text {
        max-width: 215px;
        margin: 0 auto;
    }

    @media(--phone) {
        &__block {
            max-width: 50%;
        }

        &__head {
            padding-bottom: 20px;
        }
    }

    @media(--sm-phone) {

        &__inner {
            flex-direction: column;
        }

        &__block {
            max-width: initial;
        }
    }
}