.cases {
    padding-top: 60px;

    &__container {}

    &__wrapper {}

    &__head {
        padding-bottom: 70px;
        font-size: 28px;
        font-weight: 400;
    }

    &__inner {
        display: flex;
        flex-flow: column;
    }

    &__block {
        display: flex;
        flex-flow: row wrap;
        margin-bottom: 70px;
    }

    &__info {
        padding-top: 30px;
        padding-bottom: 15px;
        flex: 1 0 0;
        display: flex;
        flex-direction: column;
    }

    &__image {
        max-width: 555px;
        width: 100%;
        height: 265px;
        // object-fit: cover;
        position: relative;
        overflow: hidden;

        img {
            position: absolute;
            left: 50%;
            top: 50%;
            width: 100%;
            max-width: 100%;
            transform: translate(-50%, -50%);
        }
    }

    &__title {
        font-size: 20px;
        font-weight: 500;
        margin: 0 0 10px;
    }

    &__descr {
        color: rgba(#000, .7);
        margin: 0 0 30px;
        font-size: 18px;
    }

    &__category {
        color: rgba(#000, .7);
        flex: 1 0 0;
    }

    &__link {
        text-transform: uppercase;
        font-size: 10px;
        color: rgba(#888888, .7);

        &:hover {
            color: #000;
        }

    }

    @media(max-width: 960px) {
        &__block {
            flex-direction: column-reverse;
            border-bottom: 1px solid rgba(#000, .5);
            margin-bottom: 30px;
        }

        &__info {
            flex: initial;
        }

        &__image {
            max-width: initial;
        }

        &__descr {
            margin-bottom: 10px;
        }

        &__category {
            margin-bottom: 20px;
        }

        &__link {
            display: inline-flex;
            background-color: transparent;
            border: 1px solid transparent;
            outline: none;
            justify-content: center;
            align-items: center;
            cursor: pointer;
            font-family: Stolzl, Roboto, sans-serif;
            transition: .3s;
            height: 50px;
            width: 210px;
            font-size: 16px;
            background-color: #1e55d2;
            color: #fff;
        }
    }

    @media(--phone) {
        &__inner {
            flex-flow: column wrap;
        }

        &__head {
            padding-bottom: 20px;
        }
    }

    @media(--sm-phone) {
        &__link {
            width: 100%;
        }
    }
}