html {
	height: 100%;
}

body {
	font-size: 14px;
	font-family: 'Stolzl', 'Roboto', sans-serif;
	padding: 0;
	margin: 0;

	&.fixed {
		position: absolute;
		max-height: 100vh;
		overflow: hidden;
	}
}

* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

button,
input {
	background-color: transparent;
	border: none;
}

ol,
ul,
li {
	padding: 0;
	margin: 0;
	list-style: none;
}

a {
	text-decoration: none;

	&:hover {
		text-decoration: none;
	}
}

img {
	max-width: 100%;
}

.container {
	max-width: 1170px;
	width: 100%;
	padding: 0 15px;
	margin: 0 auto;
	display: flex;
	flex-direction: column;
	position: relative;
}