@import url("https://fonts.googleapis.com/css2?family=PT+Sans:wght@400;700&display=swap");

@font-face {
    font-family: "Stolzl";
    src: local("Stolzl Regular"), local("Stolzl Regular"),
        url("../fonts/Stolzl-Regular/Stolzl-Regular.woff2") format("woff2"),
        url("../fonts/Stolzl-Regular/Stolzl-Regular.woff") format("woff"),
        url("../fonts/Stolzl-Regular/Stolzl-Regular.ttf") format("truetype");
    font-weight: 400;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: "Stolzl";
    src: local("Stolzl Light"), local("Stolzl-Light"),
        url("../fonts/Stolzl-Light/Stolzl-Light.woff2") format("woff2"),
        url("../fonts/Stolzl-Light/Stolzl-Light.woff") format("woff"),
        url("../fonts/Stolzl-Light/Stolzl-Light.ttf") format("truetype");
    font-weight: 300;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: "Stolzl";
    src: local("Stolzl Book"), local("Stolzl-Book"),
        url("../fonts/Stolzl-Book/Stolzl-Book.woff2") format("woff2"),
        url("../fonts/Stolzl-Book/Stolzl-Book.woff") format("woff"),
        url("../fonts/Stolzl-Book/Stolzl-Book.ttf") format("truetype");
    font-weight: 500;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: "Stolzl";
    src: local("Stolzl Bold"), local("Stolzl-Bold"),
        url("../fonts/Stolzl-Bold/Stolzl-Bold.woff2") format("woff2"),
        url("../fonts/Stolzl-Bold/Stolzl-Bold.woff") format("woff"),
        url("../fonts/Stolzl-Bold/Stolzl-Bold.ttf") format("truetype");
    font-weight: 700;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'Cocogoose Pro Light';
    font-style: normal;
    font-weight: normal;
    src: local('Cocogoose Pro Light'),
        url('../fonts/CocogooseProSemiLightTrial/CocogooseProSemiLightTrial.woff') format('woff'),
        url("../fonts/CocogooseProSemiLightTrial/CocogooseProSemiLightTrial.woff2") format("woff2"),
        url("../fonts/CocogooseProSemiLightTrial/CocogooseProSemiLightTrial.ttf") format("truetype");

}