.reviews {
    padding: 55px 0 40px;

    &__container {}

    &__inner {
        display: flex;
        flex-flow: row wrap;
        // margin-bottom: -30px;
        margin-bottom: 50px;
        max-height: 370px;
        overflow: hidden;
        transition: .5s;

        &.active {
            max-height: 1500px;
        }
    }

    &__block {
        max-width: calc(50% - 30px);
        width: 100%;
        margin: 0 15px 30px;
    }

    &__info {
        display: flex;
        flex-flow: row wrap;
        align-items: center;
        margin-bottom: 35px;
    }

    &__avatar {
        width: 87px;
        height: 87px;
        text-align: center;
        line-height: 87px;
        overflow: hidden;
        border-radius: 999px;
        margin-right: 20px;
    }

    &__name {
        p {
            font-size: 18px;
            margin-bottom: 10px;
        }

        span {
            color: rgba(#000, .7);
        }
    }

    &__text {
        padding: 30px 30px 50px;
        border-radius: 10px;
        font-size: 14px;
        font-weight: 500;
        line-height: 1.9em;
        background-color: #eaeaea;
        position: relative;

        &:after {
            content: "";
            position: absolute;
            left: 36px;
            top: -23px;
            border: 9px solid transparent;
            border-bottom: 15px solid #eaeaea;
        }
    }

    &__more {
        text-align: center;
        font-weight: 500;

        a {
            color: rgba(#000, .7);
            text-transform: uppercase;
            text-decoration: underline;

            &:hover {
                text-decoration: none;
            }
        }
    }

    @media(--tablet) {
        &__block {
            margin: 0 0 30px;
            max-width: initial;
        }
    }

    @media(--phone) {
        &__text {
            padding: 20px;
        }
    }
}