.footer {
    padding: 35px 0;
    background-color: #161616;

    &__wrapper {
        display: flex;
        flex-flow: row wrap;
        align-items: center;
        width: 100%;
        margin-bottom: 30px;
    }

    &__logo {
        font-family: 'Cocogoose Pro Light';
        margin-right: 70px;
        line-height: 1.05em;
        color: #fff;
    }

    &__nav {
        font-family: 'PT Sans', sans-serif;
        flex: 1 0 0;

        &-list {
            display: flex;
            align-items: center;
        }

        &-item {
            margin-right: 25px;

            &:last-of-type {
                margin-right: 0;
            }
        }

        &-link {
            color: #fff;
            font-size: 16px;

            &:hover:not(.active) {
                text-decoration: underline;

            }

            &.active {
                font-weight: 700;
                pointer-events: none;
            }
        }
    }

    &__right {
        display: flex;
        align-items: center;
    }

    &__contact {
        text-align: right;
        margin-right: 15px;

        a {
            color: #fff;
            font-size: 14px;
            display: block;
            line-height: 22px;

            &:hover {
                text-decoration: underline;
            }
        }

        span {
            display: block;
            color: #fff;
        }
    }

    &__button {
        width: 130px;
        height: 30px;
        color: #fff;
        border-color: var(--blue);

        &:hover {
            background-color: var(--blue);
            color: #fff;
        }
    }

    &__copyright {
        width: 100%;
        font-weight: 300;
        color: rgba(#fff, .9);
        text-align: center;
    }

    @media (--tablet) {
        &__logo {
            align-self: flex-start;
            flex: 1 0 0;
            margin-right: 0;
            padding-right: 30px;
        }

        &__nav {
            flex: 1 0 auto;

            &-list {
                justify-content: flex-end;
            }
        }

        &__wrapper {
            position: relative;
            align-items: center;
        }

        &__right {
            margin-top: 20px;
            flex: 1 0 100%;
            justify-content: flex-end;
        }
    }


    @media(--sm-phone) {
        &__wrapper {
            flex-direction: column;
            align-items: flex-start;
        }


        &__logo {
            margin-bottom: 20px;
        }

        &__contact {
            text-align: left;
        }
    }
}