.header {

    &__wrapper {
        position: absolute;
        z-index: 10;
        top: 0;
        left: 15px;
        display: flex;
        flex-flow: row wrap;
        align-items: flex-end;
        width: calc(100% - 30px);
        padding-top: 25px;
    }

    &__logo {
        font-family: 'Cocogoose Pro Light';
        margin-right: 70px;
        line-height: 1.05em;
    }

    &__nav {
        font-family: 'PT Sans', sans-serif;
        flex: 1 0 0;

        &-list {
            display: flex;
            align-items: center;
        }

        &-item {
            margin-right: 25px;

            &:last-of-type {
                margin-right: 0;
            }
        }

        &-link {
            color: #000;
            font-size: 16px;

            // &:hover:not(.active) {
            //     text-decoration: underline;

            // }
            &:hover {
                font-weight: 700;
            }

            &.active {
                font-weight: 700;
                pointer-events: none;
            }
        }
    }

    &__right {
        display: flex;
        align-items: center;
    }

    &__contact {
        text-align: right;
        margin-right: 15px;

        a {
            color: #000;
            font-size: 14px;
            display: block;
            line-height: 22px;

            &:hover {
                text-decoration: underline;
            }
        }

        span {
            display: block;
            color: #636363;
        }
    }

    &__button {
        width: 130px;
        height: 30px;
        color: var(--blue);
        border-color: var(--blue);

        &:hover {
            background-color: var(--blue);
            color: #fff;
        }
    }

    &__burger {
        display: none;
        width: 30px;
        height: 30px;
        position: relative;
        transform: translateY(-5px);

        &.active {
            span {
                transform: translateY(-50%) rotate(45deg);

                &:after {
                    top: 0;
                    transform: rotate(90deg);
                }

                &:before {
                    top: 0;
                    transform: rotate(90deg);
                }
            }
        }

        span {
            display: block;
            width: 100%;
            height: 2px;
            background-color: #000;
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            transition: .3s;

            &:after,
            &:before {
                content: '';
                width: 100%;
                height: 2px;
                background-color: #000;
                left: 0;
                right: 0;
                display: block;
                position: absolute;
                transition: .3s;
            }

            &:after {
                top: -8px;
            }

            &:before {
                top: 8px;
            }
        }
    }

    @media (--tablet) {
        &__burger {
            display: block;
        }

        &__wrapper {
            position: fixed;
            background-color: #fff;
            padding: 25px 15px 25px;
            width: 100%;
            left: 0px;
        }

        &__nav {
            position: fixed;
            right: 0;
            top: 100px;
            transform: translateX(100%);
            transition: .5s;
            background-color: #fff;
            border: 1px solid var(--blue);
            padding: 20px;
            min-width: 320px;
            border-radius: 5px;

            &.active {
                transform: translateX(-2%);

            }

            &-list {
                flex-direction: column;
                align-items: flex-start;
            }

            &-item {
                margin-right: 0;
                margin-bottom: 10px;
            }

            &-link {
                &.active {
                    padding-left: 5px;
                }
            }
        }

        &__right {
            flex: 1 0 auto;
            justify-content: flex-end;
        }

        &__logo {
            margin-right: 40px;
        }
    }

    @media(--phone) {
        &__wrapper {
            box-shadow: 0 5px 10px rgba(#000, .1);
        }

        &__logo {
            flex: 1 0 0;
        }

        &__right {
            // width: 100%;
            // margin-top: 10px;
            // justify-content: flex-start;
            display: none;
        }

        &__contact {
            text-align: left;
        }

        &__nav {
            top: 80px;
            width: 100%;
            height: calc(100% - 80px);
            border-radius: 0;

            &.active {
                transform: translateX(-0%);
            }
        }
    }

    @media(--sm-phone) {

        &__nav {
            top: 92px;
            height: 100vh;

        }
    }
}