.feedback {
    padding: 75px 0 80px;
    background-color: #ebebeb;

    &__container {}

    &__wrapper {}

    &__head {
        margin-bottom: 40px;

        span {
            text-transform: uppercase;
            color: rgba(#000, .7);
            display: block;
            margin-bottom: 10px;
        }

        p {
            font-size: 18px;
            font-weight: 500;
        }
    }

    &__form {
        max-width: calc(585px + 50px);
        width: 100%;
        margin: 0 -25px;
        display: flex;
        flex-flow: row wrap;

        &-field {
            margin: 0 25px 50px;
            max-width: calc(50% - 50px);
            width: 100%;
        }

        &-input {
            font-size: 18px;
            width: 100%;
            font-weight: 500;
            border-bottom: 2px solid rgba(#6a6a6a, .7);
            padding-bottom: 10px;
            outline: none;

            &:focus {
                border-bottom-color: #6a6a6a;
            }
        }

        &-submit {
            display: flex;
            flex-flow: row nowrap;
            align-items: center;
            width: 100%;
            padding: 0 25px;

            button {
                max-width: 210px;
                width: 100%;
                height: 50px;
                background-color: var(--blue);
                color: #fff;
                margin-right: 30px;

                &:hover {
                    color: var(--blue);
                    border-color: var(--blue);
                    background-color: transparent;
                }
            }

            span {
                flex: 1 0 0;
            }
        }
    }

    @media(--phone) {
        padding: 25px 0 30px;

        &__form {
            max-width: initial;
            margin: 0;

            &-field {
                max-width: initial;
                margin: 0 0 30px;
            }

            &-submit {
                padding: 0;
            }
        }
    }

    @media(--sm-phone) {
        &__form {
            &-submit {
                flex-flow: column-reverse wrap;
                align-items: flex-start;

                span {
                    padding-bottom: 20px;
                }
            }
        }
    }
}