.services {
    padding-top: 60px;
    padding-bottom: 60px;
    background-color: #161616;
    position: relative;

    &__container {}

    &__wrapper {}

    &__head {
        padding-bottom: 70px;
        font-size: 28px;
        font-weight: 500;
        color: #fff;
    }

    &__inner {
        display: flex;
        flex-flow: row wrap;
    }

    &__block {
        max-width: calc(50% - 10px);
        margin: 0 5px 10px;
        width: 100%;
        background-color: #fff;
        padding: 25px 0;
        padding-left: 45px;
        display: flex;
        flex-flow: column;
        height: 235px;
        border-radius: 14px;
        background-repeat: no-repeat;
        background-position: 100% 100%;
        // background-size: 270px;
        background-size: contain;

    }

    &__name {
        font-size: 18px;
        margin-bottom: 20px;
        font-weight: 500;
    }

    &__description {
        flex: 1 0 0;
        max-width: 200px;
        color: rgba(#000, .74);
    }

    &__link {
        color: rgba(#000, .7);
        text-transform: uppercase;

        &:hover {
            color: #000;
        }
    }

    &__modal {
        display: none;
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(#000, .5);
        z-index: 1;
        transition: .3s;


        &.active {
            display: block;
        }

        &-content {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            background-color: #fff;
            max-width: 1070px;
            width: 100%;
            padding: 35px 0;
            padding-left: 85px;
            background-repeat: no-repeat;
            background-position: 100% 50%;
        }

        &-title {
            font-size: 36px;
            font-weight: 400;
            margin-bottom: 20px;
        }

        &-descr {
            font-size: 14px;
            color: rgba(#000, .7);
            margin-bottom: 20px;
            display: block;
        }

        &-text {
            font-size: 14px;
            margin-bottom: 50px;
            font-weight: 500;
            max-width: 480px;
        }

        &-bottom {
            display: flex;
            flex-flow: row wrap;
            align-items: center;

            a {
                background-color: #1e55d2;
                color: #fff;
                margin-right: 25px;
                height: 50px;
                width: 210px;
                font-size: 16px;
            }

            p {
                font-weight: 500;
                font-size: 18px;
            }
        }

        &-close {
            position: absolute;
            right: 10px;
            top: 10px;
            width: 50px;
            height: 50px;
            background-image: url('../images/back.png');
            background-repeat: no-repeat;
            background-position: center;
            background-size: 30px;
            cursor: pointer;
            z-index: 1;
        }


    }

    @media(--tablet) {
        &__inner {
            flex-wrap: nowrap;
            overflow-x: auto;
        }

        &__block {
            max-width: calc(60% - 10px);
            flex: 1 0 auto;
        }
    }

    @media(--phone) {
        &__inner {
            flex-flow: column wrap;
        }

        &__block {
            max-width: initial;
            margin: 0 0 10px;
            padding: 25px 0 25px 25px;
        }

        &__head {
            padding-bottom: 20px;
        }

        &__modal {
            position: fixed;
            top: 50%;
            transform: translateY(-50%);

            &-content {
                background-image: none !important;
                padding: 25px;
            }

            &-title {
                font-size: 26px;
            }

            &-text {
                margin-bottom: 20px;
            }
        }
    }

    @media(--sm-phone) {
        padding-bottom: 30px;

        &__block {
            background-image: none !important;
            height: initial;
        }

        &__description {
            margin-bottom: 15px;
            max-width: initial;
        }

        &__block {
            padding: 15px;
        }


        &__link {
            display: inline-flex;
            background-color: transparent;
            border: 1px solid transparent;
            outline: none;
            justify-content: center;
            align-items: center;
            cursor: pointer;
            font-family: Stolzl, Roboto, sans-serif;
            transition: .3s;
            height: 50px;
            width: 100%;
            font-size: 16px;
            background-color: #1e55d2;
            color: #fff;
        }
    }
}